.section{
    width: 95vw;
    overflow: hidden;
    height: 100vh;
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
}
.prof_cont{
    min-width: 30%;
    display: flex;
    justify-content: center;
}
.prof_cont_img{
    width: 20rem;
    height: 20rem;
    margin-top: 2rem;
    border-radius: 50%;
    overflow: hidden;
    position: relative;
}
.prof_img{
    width: 106%;
    position: absolute;
    background-position: center;
}
.input_cont{
    width: 70%;
    /* background-color: blue; */
    padding: 2rem 7rem 0rem 8rem ;
}
input{
    outline: none;
    font-size: 1.2rem;
    color: rgb(92, 92, 92);
    margin-bottom: 1.5rem;
    border: 2px solid rgb(29, 29, 29);
    background-color: rgb(29, 29, 29);
    border-radius: 0.7rem;
    padding: 0px 5px 0px 5px;
}
.input_first{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.input_first >input{
    min-width: 48%;
    height: 2.6rem;
}
.input_sec>input{
    width: 100%;
    height: 2.6rem;
}
textarea{
    width: 100%;
    height: 14rem;
    border: 2px solid rgb(29,29,29);
    color: rgb(92, 92, 92);
    background-color: rgb(29,29,29);
    border-radius: 0.7rem;
    outline: none;
    font-size: 1.2rem;
    padding: 5px 5px 5px 5px;

}
button[type='submit']{
    background-color: #B47124;
    width: 8rem;
    border: none;
    border-radius: 0.3rem;
    margin: 1rem;
    height: 3rem;
    cursor: pointer;
}
.button{
    text-align: end;
}
button[type='submit']:hover{
    background-color: #934e00;
}
.end_cont{
    display: flex;
    flex-wrap: wrap;
}




@media only screen and (max-width: 700px) {
    .section{justify-content: center;
    height: auto;}
    .input_cont{
        padding: 1rem;
        width: 80%;
        /* background-color: blue; */
    }
    .input_first >input{
        min-width: 100%;
        height: 3rem;
    }
    .end_cont{
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        width: 100%;
    }
    .end_right{
        width: 100%;
    }
    .map_cont{
        margin-top: 2.5rem;
        width: 100%;
    }
}